import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Spinner } from "react-bootstrap";
import { getSystems, getSourceTable, getCassandraTable, runInitJob, processFile } from '../redux/actions';
import { InitForm } from "../components";
import { PopUp } from "../shared";

class Init extends React.Component {
    constructor(props) {
        super(props);

        this.getInitialState = this.getInitialState.bind(this);
        this.onChange = this.onChange.bind(this);
        this.reset = this.reset.bind(this);
        this.runJob = this.runJob.bind(this);
        this.processFile = this.processFile.bind(this);
        this.onPopupClose = this.onPopupClose.bind(this);

        this.state = this.getInitialState();
    }

    getInitialState() {
        return {
            ...this.props.initData
        }
    }

    reset(fieldName = '') {
        let formField = { ...this.state.formField };
        let formErrors = { ...this.state.formErrors };
        switch (fieldName) {
            case 'system':
                formField.system = '';
                formErrors.system = '';
                break;
            case 'sourceTable':
                formField.sourceTable = '';
                formErrors.sourceTable = '';
                break;
            case 'cassandraTable':
                formField.cassandraTable = '';
                formErrors.cassandraTable = '';
                break;
            case 'environment':
                formField.environment = '';
                formErrors.environment = '';
                break;
            case 'instance':
                formField.instance = '';
                formErrors.instance = '';
                break;
            default:
                Object.keys(formField).map((element) => {
                    formField[element] = '';
                    return formField;
                })
                Object.keys(formErrors).map((element) => {
                    formErrors[element] = '';
                    return formErrors;
                })
                break;
        }
        this.setState({
            formErrors: formErrors,
            formField: formField,
            disableRunInitJob: true,
            disableProccessInitFile: true
        });
    }

    async onChange(event) {
        // Set form field value
        const { name, value } = event.target;

        await this.setState(prevState => {
            let myOldData = { ...prevState.formField };
            myOldData[name] = value;
            return {
                formField: myOldData
            };
        });
        // Fetch dependent data and set validation messages
        const { valid, validationMessage } = event.validationData;
        let formErrors = { ...this.state.formErrors };
        switch (name) {
            case 'system':
                this.reset('sourceTable');
                this.reset('cassandraTable');
                this.reset('instance');
                formErrors.system = (!valid && validationMessage) ? validationMessage : '';
                this.props.getSourceTable({ systemId: value.value });
                break;
            case 'sourceTable':
                this.reset('cassandraTable');
                this.reset('instance');
                let selectedSourceTable = [];
                if (value && Array.isArray(value)) {
                    value.map((ele) => {
                        selectedSourceTable.push(ele.value);
                        return selectedSourceTable;
                    });
                }
                formErrors.sourceTable = (!valid && validationMessage) ? validationMessage : '';
                this.props.getCassandraTable({ sourceTableId: selectedSourceTable });
                break;
            case 'cassandraTable':
                formErrors.cassandraTable = (!valid && validationMessage) ? validationMessage : '';
                break;
            case 'environment':
                formErrors.environment = (!valid && validationMessage) ? validationMessage : '';
                break;
            case 'instance':
                formErrors.instance = (!valid && validationMessage) ? validationMessage : '';
                break;
            default:
                break;
        }
        // Check form is valid or not and enable/disable the button
        let formValid = [];
        if (this.state.formField.system && this.state.formField.sourceTable && this.state.formField.cassandraTable && this.state.formField.environment && this.state.formField.instance) {
            Object.keys(formErrors).map((element, index) => {
                if (formErrors[element] === '') {
                    formValid.push(true);
                } else {
                    formValid.push(false);
                }
                return formValid;
            });
        } else {
            formValid.push(false);
        }
        // Update related values
        this.setState({
            formErrors: formErrors,
            disableRunInitJob: formValid.indexOf(false) >= 0 ? true : false,
            disableProccessInitFile: formValid.indexOf(false) >= 0 ? true : false
        });
    }

    runJob() {
        console.log(this.state);
    }

    processFile() {
        const { system, sourceTable, cassandraTable, environment, instance } = this.state.formField;
        let sourceTables = [];
        sourceTable.map(table => sourceTables.push(table.value));

        let cassandraTables = [];
        cassandraTable.map(table => cassandraTables.push(table.value));

        const req = {
            systemId: system.value,
            sourceTable: sourceTables,
            cassandraTable: cassandraTables,
            environmentName: environment.value,
            instance: instance.value,
            isRecon: 0,
            frequency: null,
            feedFileName: null,
            jobRunId: 1
        };
        this.props.processFile(req);
        if (this.props.commonData.processFilePayload || this.props.commonData.processFileError || this.props.commonData.isLoading === false) {
            this.handelProcessFileResPopup();
        } else {
            setTimeout(function () {
                this.handelProcessFileResPopup();
            }.bind(this), 5000);
        }
    }

    handelProcessFileResPopup() {
        const isError = ((this.props.commonData && this.props.commonData.processFileError) || (this.props.commonData.processFilePayload && this.props.commonData.processFilePayload.status !== 200));
        if (!isError) {
            this.reset();
        }
        const popUp = {
            display: "show",
            title: !isError ? 'Success' : 'Error',
            body: !isError ? 'File Processing is in progress, please do not close the browser.' : 'Error to submit request',
            confirmBtn: "Okay",
            cancelBtn: "",
        };
        this.setState({
            popUpData: popUp
        });
    }

    onPopupClose() {
        const popUp = {
            display: "hide",
            title: "",
            body: "",
            confirmBtn: "",
            cancelBtn: "",
        };
        this.setState({
            popUpData: popUp
        });
        const isError = ((this.props.commonData && this.props.commonData.processFileError) || (this.props.commonData.processFilePayload && this.props.commonData.processFilePayload.status !== 200));
        if(!isError) {
            this.props.history.push('/home');
        }
    }

    render() {
        const formData = {
            ...this.state,
            ...this.props.commonData
        };
        const { popUpData } = this.state;
        return (
            <div className="row justify-content-md-center">
                {
                    this.props.commonData.isLoading === true
                    &&
                    <div className="spinnerWrapper">
                        <Spinner className="spinner" animation="border" role="status">
                            <span className="sr-only">Loading...</span>
                        </Spinner>
                    </div>
                }
                {
                    (this.props && this.props.commonData && this.props.initData)
                    &&
                    <InitForm onChange={this.onChange} reset={this.reset} runJob={this.runJob} processFile={this.processFile} formData={formData}></InitForm>
                }
                {
                    (popUpData && popUpData.display === 'show')
                    &&
                    <PopUp
                        title={popUpData.title}
                        body={popUpData.body}
                        confirmBtn={popUpData.confirmBtn}
                        cancelBtn={popUpData.cancelBtn}
                        onClose={this.onPopupClose}
                        show={popUpData.display === 'show' ? true : false}
                        display={popUpData.display} />
                }
            </div>
        );
    }

    componentDidMount() {
        this.props.getSystems();
    }
}

Init.propTypes = {
    getSystems: PropTypes.func.isRequired,
    getSourceTable: PropTypes.func.isRequired,
    getCassandraTable: PropTypes.func.isRequired,
    runInitJob: PropTypes.func.isRequired,
    processFile: PropTypes.func.isRequired,

    commonData: PropTypes.any,
    initData: PropTypes.any,
};

const mapStateToProps = state => ({
    commonData: state.commonDataReducer,
    initData: state.initReducer
});

const mapDispatchToProps = {
    getSystems,
    getSourceTable,
    getCassandraTable,
    runInitJob,
    processFile
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Init);