/***
This is reusable select dropdown component
Developed using react-select package

Accept dropdown options in following format,


Accept validation property value in following format,
[
    { name: 'required' },
    { name: 'minlength', minlength: 2 },
    { name: 'maxlength', maxlength: 5 }
]

***/

import React from "react";
import PropTypes from "prop-types";
import Select from 'react-select';
import makeAnimated from 'react-select/animated';

class ReactSelect extends React.Component {
    constructor(props) {
        super(props);

        this.onChange = this.onChange.bind(this);
        this.validateSelect = this.validateSelect.bind(this);
    }

    onChange(e) {
        this.props.onChangelHandel({
            validationData: this.validateSelect(e),
            target: {
                name: this.props.nameValue,
                value: e ? e : ''
            }
        })
    }

    validateSelect(e) {
        let validationData = [{ valid: false, validationMessage: '' }];
        if (this.props.validations && this.props.validations.length > 0) {
            if (this.props.validations.find(({ name }) => name === 'required')) {
                validationData.valid = (e && (e.value || e.length > 0)) ? true : false;
                validationData.validationMessage = validationData.valid ? '' : 'Please select ' + this.props.nameValue + ' value';
            } else if (this.props.validations.find(({ name }) => name === 'minlength')) {
                let element = this.props.validations.find(({ name }) => name === 'minlength');
                validationData.valid = (this.props.isMulti && e && e.length >= element.minlength) ? true : false;
                validationData.validationMessage = validationData.valid ? '' : 'Please select minimum ' + element.minlength + ' ' + this.props.nameValue + ' value';
            } else if (this.props.validations.find(({ name }) => name === 'maxlength')) {
                let element = this.props.validations.find(({ name }) => name === 'minlength');
                validationData.valid = (this.props.isMulti && e && e.length >= element.maxlength) ? true : false;
                validationData.validationMessage = validationData.valid ? '' : 'You can select maximum ' + element.maxlength + ' ' + this.props.nameValue + ' value';
            }
        }
        return validationData;
    }

    render() {
        const { nameValue, options, placeholder, value, disabled, isMulti } = this.props;
        const animatedComponents = makeAnimated();
        return (
            <React.Fragment>
                <Select
                    name={nameValue}
                    id={nameValue}
                    placeholder={placeholder}
                    onChange={this.onChange}
                    onClick={this.onChange}
                    isDisabled ={disabled ? true : false}
                    value={value}
                    options={options} // {value, label, color, isFixed}

                    isMulti={isMulti}
                    closeMenuOnSelect={isMulti ? false : true}
                    components={animatedComponents}
                />
            </React.Fragment>
        );
    }
}

ReactSelect.propTypes = {
    onChangelHandel: PropTypes.func.isRequired,
    nameValue: PropTypes.string.isRequired,
    options: PropTypes.any.isRequired,
    placeholder: PropTypes.string.isRequired,
    value: PropTypes.any,
    disabled: PropTypes.bool,
    isMulti: PropTypes.bool,
    validations: PropTypes.any
}

export default ReactSelect;