import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

import * as routes from '../shared/constants';

import { Home, Init, ReCon } from '../containers';

class DashboardRoutes extends Component {
    render() {
        return (
            <div className="container-fluid py-3">
                <Switch>
                    <Route exact path={routes.ROOT_ROUTE} component={Home} />
                    <Route exact path={routes.HOME} component={Home} />
                    <Route exact path={routes.RE_CON_FORM} component={ReCon} />
                    <Route path={routes.INIT_FORM} component={Init} />
                </Switch>
            </div>
        );
    }
}

export default DashboardRoutes;