import React from "react";
import PropTypes from "prop-types";
import { ReactSelect, Button } from "../shared";

class ReConForm extends React.Component {
    render() {
        const { systems, sourceTable, cassandraTable, environments, instances, frequency } = this.props.formData.staticData;
        const { formField, formErrors, disableRunReConJob, disableProccessReConFile } = this.props.formData;

        let disableInstanceField = false;
        if(formField && formField.sourceTable && Array.isArray(formField.sourceTable)){
            const tempArr = formField.sourceTable.filter(value => value.ref && value.ref === true);
            disableInstanceField = tempArr.length > formField.sourceTable.length;
        }
        return (
            <form className="col-lg-4 col-md-8 col-sm-12 border">
                <div className="form-group">
                <label>System <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="system" options={systems} placeholder="Select System" onChangelHandel={this.props.onChange} validations={[{ name: 'required' }]} value={formField.system} />
                    {formErrors.system && <div className="text-danger">{formErrors.system}</div>}
                </div>
                <div className="form-group">
                    <label>Source Table <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="sourceTable" options={sourceTable} placeholder="Select Source Tables" onChangelHandel={this.props.onChange} isMulti={true} validations={[{ name: 'required' }]} value={formField.sourceTable} />
                    {formErrors.sourceTable && <div className="text-danger">{formErrors.sourceTable}</div>}
                </div>
                <div className="form-group">
                    <label>Cassandra Table <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="cassandraTable" options={cassandraTable} placeholder="Select Cassandra Tables" onChangelHandel={this.props.onChange} isMulti={true} validations={[{ name: 'required' }]} value={formField.cassandraTable} />
                    {formErrors.cassandraTable && <div className="text-danger">{formErrors.cassandraTable}</div>}
                </div>
                <div className="form-group">
                    <label>Environment <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="environment" options={environments} placeholder="Select Environment" onChangelHandel={this.props.onChange} validations={[{ name: 'required' }]} value={formField.environment} />
                    {formErrors.environments && <div className="text-danger">{formErrors.environment}</div>}
                </div>
                <div className="form-group">
                    <label>Instance <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="instance" options={instances} placeholder="Select Instance" onChangelHandel={this.props.onChange} validations={[{ name: 'required' }]} value={disableInstanceField ? {value: 'East', label: 'East'} : formField.instance} disabled={disableInstanceField ? true : false} />
                    {formErrors.instances && <div className="text-danger">{formErrors.instance}</div>}
                </div>
                <div className="form-group">
                    <label>Frequency <span className="text-danger">*</span></label>
                    <ReactSelect nameValue="frequency" options={frequency} placeholder="Select Frequency" onChangelHandel={this.props.onChange} value={formField.frequency} validations={[{name: 'required'}]}/>
                    {formErrors.frequency && <div className="text-danger">{formErrors.frequency}</div>}
                </div>
                <div className="form-group text-right">
                    <Button nameValue="recon-reset" classValue="secondary mr-2" buttonName="Reset" onClickHandel={this.props.reset} />
                    <Button nameValue="recon-job-submit" classValue="primary mr-2" buttonName="Run ReCon Job" onClickHandel={this.props.runJob} disabled={disableRunReConJob} />
                    <Button nameValue="recon-process-submit" classValue="primary mr-2" buttonName="Process ReCon File" onClickHandel={this.props.processFile} disabled={disableProccessReConFile} />
                </div>
            </form>
        );
    }
}

ReConForm.propTypes = {
    onChange: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    runJob: PropTypes.func.isRequired,
    processFile: PropTypes.func.isRequired,
    formData: PropTypes.any
};

export default ReConForm;
