import React from "react";
import PropTypes from "prop-types";

class Date extends React.Component {
    render() {
        const { nameValue, placeholder, readOnly, minDate, maxDate, value } = this.props;
        return (
            <input
                type="date"
                className="form-control"
                name={nameValue}
                id={nameValue}
                placeholder={placeholder}
                onChange={this.props.onChangelHandel}
                onInput={this.props.onChangelHandel}
                readOnly={readOnly ? readOnly : false}
                min={minDate ? minDate : null}
                max={maxDate ? maxDate : null}
                value={value ? value : ""}
            />
        );
    }
}

Date.propTypes = {
    nameValue: PropTypes.string.isRequired,
    placeholder: PropTypes.string.isRequired,
    minDate: PropTypes.string,
    maxDate: PropTypes.string,
    readOnly: PropTypes.bool,
    value: PropTypes.string,
    onChangelHandel: PropTypes.func.isRequired,
}

export default Date;