import React from "react";
import { NavLink } from 'react-router-dom';
import * as routes from '../constants';
import { Navbar, NavDropdown } from 'react-bootstrap';
import '../../assets/css/layout.css';

class Header extends React.Component {
    constructor() {
        super();
        this.state = {
            sideNavDisplay: false
        }
        this.toggleSideNav = this.toggleSideNav.bind(this);
    }

    toggleSideNav() {
        this.setState({
            sideNavDisplay: !this.state.sideNavDisplay
        });
    }

    render() {
        return (
            <React.Fragment>
                <header>
                    <Navbar className="navbar" expand="lg" fixed="top"> {/*fixed="top"*/}
                        <Navbar.Toggle className="brand mr-2" onClick={this.toggleSideNav} />
                        <Navbar.Brand className="brand mr-auto">
                            <NavLink to={routes.HOME} className="brand">
                                CASSANDRA UI TOOL
                            </NavLink>
                        </Navbar.Brand>
                        {/* <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcScN-ajOppTloQu279NdeQ7GKS7PNK8T4wrsKSkPPUaG-GxxJGj&s" id="userProfileIcon" /> */}

                        <NavDropdown title="FirstName LastName" id="basic-nav-dropdown">
                            <NavDropdown.Item href={routes.HOME}>My Profile</NavDropdown.Item>
                            <NavDropdown.Item href={routes.HOME}>Log Out</NavDropdown.Item>
                        </NavDropdown>
                    </Navbar>
                </header>
                    <div id={this.state.sideNavDisplay ? "mySidenav" : ""} className="sidenav">
                        <NavLink to={routes.HOME} onClick={this.toggleSideNav}>Home</NavLink>
                        <NavLink to={routes.INIT_FORM} onClick={this.toggleSideNav}>Init</NavLink>
                        <NavLink to={routes.RE_CON_FORM} onClick={this.toggleSideNav}>ReCon</NavLink>
                    </div>
            </React.Fragment>
        );
    }
}

export default Header;