import * as types from '../../shared/constants';
import initialState from './initialState';

const commonDataReducer = (state = initialState.commonData, action) => {
    switch (action.type) {
        case types.GET_SYSTEM_STARTED: case types.GET_SOURCE_TABLE_STARTED: case types.GET_CASSANDRA_TABLE_STARTED: case types.PROCESS_FILE_STARTED:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case types.GET_SYSTEM_SUCCEEDED:
            return {
                ...state,
                staticData: {
                    ...state.staticData,
                    systems: action.getSystemsPayload.systems ? action.getSystemsPayload.systems.map((element, index) => {
                        return {
                            value: element.systemId,
                            label: element.systemName,
                        }
                    }) : [],
                },
                isLoading: false,
                error: false
            }
        case types.GET_SYSTEM_FAILED:
            return {
                ...state,
                isLoading: false,
                error: action.getSystemsError
            }
        case types.GET_SOURCE_TABLE_SUCCEEDED:
            return {
                ...state,
                staticData: {
                    ...state.staticData,
                    sourceTable: action.getSourceTablePayload.sourceTables ? action.getSourceTablePayload.sourceTables.map((element, index) => {
                        return {
                            value: element.sourceTableId,
                            label: element.sourceTableName,
                            ref: element.refernece
                        }
                    }) : [],
                },
                isLoading: false,
                error: false
            }
        case types.GET_SOURCE_TABLE_FAILED:
            return {
                ...state,
                isLoading: false,
                error: action.getSourceTableError
            }
        case types.GET_CASSANDRA_TABLE_SUCCEEDED:
            return {
                ...state,
                staticData: {
                    ...state.staticData,
                    cassandraTable: action.getCassandraTablePayload.cassandraTables ? action.getCassandraTablePayload.cassandraTables.map((element, index) => {
                        return {
                            value: element.cassandraTableId,
                            label: element.cassandraTableName,
                        }
                    }) : [],
                },
                isLoading: false,
                error: false
            }
        case types.GET_CASSANDRA_TABLE_FAILED:
            return {
                ...state,
                isLoading: false,
                error: action.getCassandraTableError
            }
        case types.PROCESS_FILE_SUCCEEDED:
            return {
                ...state,
                processFilePayload: action.processFilePayload,
                isLoading: false,
                error: false
            }
        case types.PROCESS_FILE_FAILED:
            return {
                ...state,
                isLoading: false,
                processFileError: action.processFileError,
                error: action.processFileError
            }
        default:
            return state;
    }
}

export default commonDataReducer;