import * as types from '../../shared/constants';

const reconFileProcessingRequestsStarted = () => ({
    type: types.RECON_FILE_PROCESSING_REQUESTS_STARTED
});

const reconFileProcessingRequestsSucceeded = data => ({
    type: types.RECON_FILE_PROCESSING_REQUESTS_SUCCEEDED,
    reconFileProcessingRequestsPayload: data
});

const reconFileProcessingRequestsFailed = error => ({
    type: types.RECON_FILE_PROCESSING_REQUESTS_FAILED,
    reconFileProcessingRequestsError: error,
    error: true
});

export const reconFileProcessingRequests = () => {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    headers.append('Access-Control-Allow-Origin', '*');
    headers.append('Access-Control-Allow-Methods','GET, POST, OPTIONS, PUT, PATCH, DELETE');
    headers.append('Access-Control-Allow-Headers',"Origin, X-Requested-With, Content-Type, Accept, x-auth");

    const reqBody = {
        isRecon : 1
    };

    return function (dispatch) {
        dispatch(reconFileProcessingRequestsStarted());
        return fetch(types.DEV_API + "/getProcessingRequests", {
            method: 'POST',
            body: JSON.stringify(reqBody),
            headers: headers
        })
            .then(
                response => response.json()
            )
            .then(
                (res) => {
                    dispatch(reconFileProcessingRequestsSucceeded(res))
                },
                (error) => {
                    dispatch(reconFileProcessingRequestsFailed(error))
                }
            );
    };
};


const runReConJobStarted = () => ({
    type: types.RUN_INIT_JOB_STARTED
});

const runReConJobSucceeded = data => ({
    type: types.RUN_INIT_JOB_SUCCEEDED,
    runReConJobPayload: data
});

const runReConJobFailed = error => ({
    type: types.RUN_INIT_JOB_FAILED,
    runReConJobError: error,
    error: true
});

export const runReConJob = (body) => {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    headers.append('Access-Control-Allow-Origin', '*');
    headers.append('Access-Control-Allow-Methods','GET, POST, OPTIONS, PUT, PATCH, DELETE');
    headers.append('Access-Control-Allow-Headers',"Origin, X-Requested-With, Content-Type, Accept, x-auth");

    return function (dispatch) {
        dispatch(runReConJobStarted());
        return fetch(types.DEV_API + "/runReConJob", {
            method: 'POST',
            body: JSON.stringify(body),
            headers: headers
        })
            .then(
                response => response.json()
            )
            .then(
                (res) => {
                    dispatch(runReConJobSucceeded(res))
                },
                (error) => {
                    dispatch(runReConJobFailed(error))
                }
            );
    };
};
