import React, { Component } from 'react';
import PropTypes from "prop-types";
import { Table } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPencilAlt, faTrashAlt, faListOl, faSort, faCaretDown, faCaretUp } from '@fortawesome/free-solid-svg-icons';

class ReactTable extends Component {
    render() {
        let { thead, tbody, actions, parentClassName } = this.props;
        return (
            <div className={"table-responsive "+parentClassName}>
                <Table responsive striped bordered size="sm" className="card-text">
                    <thead>
                        {thead && thead.map((row, i) => (
                            <tr key={i}>
                                {row.map((coln, j) => (
                                    typeof coln === 'object'
                                        ?
                                            (coln.sorting === 'ASC' || coln.sorting === 'DESC' || coln.sorting === 'NOSORT')
                                            ?
                                            <th key={j} className="text-center">
                                            {coln.thead + " "}
                                            {coln.sorting === 'ASC' && <FontAwesomeIcon icon={faCaretDown} id={"asc"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('ASC', j)} />}
                                            {coln.sorting === 'DESC' && <FontAwesomeIcon icon={faCaretUp} id={"desc"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('DESC', j)} />}
                                            {coln.sorting === 'NOSORT' && <FontAwesomeIcon icon={faSort} id={"asc"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('NOSORT', j)} />}
                                            </th>
                                            :
                                            <th className="text-center" key={j} colSpan={coln.colspan ? coln.colspan : ''} rowSpan={coln.rowspan ? coln.rowspan : ''} >{coln.thead}</th>
                                        :
                                        <th className="text-center" key={j}>{coln}</th>
                                ))}
                                {(actions && i === 0) && <th className="text-center" rowSpan={thead.length > 0 ? thead.length : ''}>Actions</th>}
                            </tr>
                        ))}
                    </thead>
                    <tbody>
                        {tbody && tbody.map((row, i) => (
                            <tr key={i}>
                                {row.map((coln, j) => (
                                    typeof coln === 'object'
                                        ?
                                        <td key={j} colSpan={coln.colspan ? coln.colspan : 0} rowSpan={coln.rowspan ? coln.rowspan : 0} >{coln.tbody}</td>
                                        :
                                        <td key={j}>{coln}</td>
                                ))}
                                {
                                    actions 
                                    && 
                                    <td>
                                        { actions.indexOf('list') > -1 && <FontAwesomeIcon icon={faListOl} id={"list"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('list', row)} />}
                                        &nbsp;
                                        { actions.indexOf('edit') > -1 && <FontAwesomeIcon icon={faPencilAlt} id={"edit"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('edit', row)} />}
                                        &nbsp;
                                        { actions.indexOf('delete') > -1 && <FontAwesomeIcon icon={faTrashAlt} id={"delete"+i} className="mr-1 clickable" onClick={() => this.props.handelAction('delete', row)} />}
                                    </td>
                                }
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </div>
        );
    }
}

ReactTable.propTypes = {
    thead: PropTypes.any.isRequired,
    tbody: PropTypes.any.isRequired,
    actions: PropTypes.any,
    handelAction: PropTypes.func,
    parentClassName: PropTypes.string
};

export default ReactTable;