import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Spinner } from "react-bootstrap";
import { initFileProcessingRequests, reconFileProcessingRequests } from '../redux/actions';
import { ReactTable } from "../shared";

class Home extends React.Component {
    constructor(){
        super();

        this.state = {
            initThead: [
                [{
                    colspan: 7,
                    thead: 'Init Record'
                }], [
                    'Id',
                    'System',
                    'Source Tables',
                    'Cassandra Tables',
                    'Environment',
                    'Instance',
                    'Status'
                ],
            ],
            reconThead: [
                [{
                    colspan: 8,
                    thead: 'ReCon Record'
                }], [
                    'Id',
                    'System',
                    'Source Tables',
                    'Cassandra Tables',
                    'Environment',
                    'Instance',
                    'Frequency',
                    'Status'
                ],
            ]
        }
    }

    render() {
        const {initThead, reconThead} = this.state;

        let initTbody = [];
        if (this.props.iniitData && this.props.iniitData.initfileProcessingRequestsPayload && this.props.iniitData.initfileProcessingRequestsPayload.fileProcessingRequests) {
            this.props.iniitData.initfileProcessingRequestsPayload.fileProcessingRequests.map(ele => {
                initTbody.push([
                    ele.id,
                    ele.systemName,
                    ele.souceTable.toString(),
                    ele.cassandraTable.toString(),
                    ele.environment,
                    ele.instance,
                    ele.status,
                ]);
                return initTbody;
            })
        }

        let reconTbody = [];
        if (this.props.reConData && this.props.reConData.reconfileProcessingRequestsPayload && this.props.reConData.reconfileProcessingRequestsPayload.fileProcessingRequests) {
            this.props.reConData.reconfileProcessingRequestsPayload.fileProcessingRequests.map(ele => {
                reconTbody.push([
                    ele.id,
                    ele.systemName,
                    ele.souceTable.toString(),
                    ele.cassandraTable.toString(),
                    ele.environment,
                    ele.instance,
                    '',
                    ele.status,
                ]);
                return reconTbody;
            })
        }        
        return (
            <div className="row justify-content-md-center">
                {
                    (this.props.commonData && this.props.commonData.isLoading === true)
                    &&
                    <div className="spinnerWrapper">
                        <Spinner className="spinner" animation="border" role="status">
                            <span className="sr-only">Loading...</span>
                        </Spinner>
                    </div>
                }

                <ReactTable thead={initThead} tbody={initTbody} parentClassName="col-11 mb-5" />
                <ReactTable thead={reconThead} tbody={reconTbody} parentClassName="col-11" />
            </div>
        );
    }

    async componentDidMount() {
        await this.props.initFileProcessingRequests();
        await this.props.reconFileProcessingRequests();
    }
}

Home.propTypes = {
    initFileProcessingRequests: PropTypes.func.isRequired,
    reconFileProcessingRequests: PropTypes.func.isRequired,

    iniitData: PropTypes.any,
    reConData: PropTypes.any,
};

const mapStateToProps = state => ({
    iniitData: state.initReducer,
    reConData: state.reConReducer,
});

const mapDispatchToProps = {
    initFileProcessingRequests,
    reconFileProcessingRequests
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home);