import * as types from '../../shared/constants';

const getSystemsStarted = () => ({
    type: types.GET_SYSTEM_STARTED
});

const getSystemsSucceeded = data => ({
    type: types.GET_SYSTEM_SUCCEEDED,
    getSystemsPayload: data
});

const getSystemsFailed = error => ({
    type: types.GET_SYSTEM_FAILED,
    getSystemsError: error,
    error: true
});

export const getSystems = () => {
    return function (dispatch) {
        dispatch(getSystemsStarted());
        return fetch(types.DEV_API + "/systems")
            .then(
                response => response.json()
            )
            .then(
                (res) => {
                    dispatch(getSystemsSucceeded(res))
                },
                (error) => {
                    dispatch(getSystemsFailed(error))
                }
            );
    };
};

const getSourceTableStarted = () => ({
    type: types.GET_SOURCE_TABLE_STARTED
});

const getSourceTableSucceeded = data => ({
    type: types.GET_SOURCE_TABLE_SUCCEEDED,
    getSourceTablePayload: data
});

const getSourceTableFailed = error => ({
    type: types.GET_SOURCE_TABLE_FAILED,
    getSourceTableError: error,
    error: true
});

export const getSourceTable = (body) => {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    headers.append('Access-Control-Allow-Origin', '*');
    headers.append('Access-Control-Allow-Methods', 'GET, POST, OPTIONS, PUT, PATCH, DELETE');
    headers.append('Access-Control-Allow-Headers', "Origin, X-Requested-With, Content-Type, Accept, x-auth");

    return function (dispatch) {
        dispatch(getSourceTableStarted());
        return fetch(types.DEV_API + "/sourceTables", {
            method: 'POST',
            body: JSON.stringify(body),
            headers: headers
        })
            .then(
                response => response.json()
            )
            .then(
                (res) => {
                    dispatch(getSourceTableSucceeded(res))
                },
                (error) => {
                    dispatch(getSourceTableFailed(error))
                }
            );
    };
};

