import * as types from '../../shared/constants';
import initialState from './initialState';

const reConReducer = (state = initialState.reConForm, action) => {
    switch (action.type) {
        case types.RECON_FILE_PROCESSING_REQUESTS_STARTED:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case types.RECON_FILE_PROCESSING_REQUESTS_SUCCEEDED:
            return {
                ...state,
                reconfileProcessingRequestsPayload: action.reconFileProcessingRequestsPayload,
                isLoading: false,
                error: false
            }
        case types.RECON_FILE_PROCESSING_REQUESTS_FAILED:
            return {
                ...state,
                isLoading: false,
                reconfileProcessingRequestsError: action.reconFileProcessingRequestsError,
                error: action.getFileProcessingRequestsError
            }

        case types.RUN_RECON_JOB_STARTED:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case types.RUN_RECON_JOB_SUCCEEDED:
            return {
                ...state,
                runReConJobPayload: action.runReConJobPayload,
                isLoading: false,
                error: false
            }
        case types.RUN_RECON_JOB_FAILED:
            return {
                ...state,
                isLoading: false,
                error: action.runReConJobError
            }
        default:
            return state;
    }
}

export default reConReducer;