import React, { Component } from "react";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import { Modal } from "react-bootstrap";

import '../../assets/css/popUp.css';

class PopUp extends Component {
    constructor() {
        super();
        this.state = {};
    }

    render() {
        let { display, title, body, confirmBtn, cancelBtn, show } = this.props;
        return (
            <React.Fragment>
                <Modal show={show} onHide={() => this.props.onClose('Cancel')} className={display + " modal"} >
                    <Modal.Header closeButton >
                        {(title && title === 'Success') && <span><FontAwesomeIcon icon={faCheck} className="modal-dialog-success mr-2" /></span>}
                        {(title && title === 'Warning') && <span><FontAwesomeIcon icon={faExclamationTriangle} className="modal-dialog-warning mr-2" /></span>}
                        {(title && title === 'Error') && <span><FontAwesomeIcon icon={faExclamationTriangle} className="modal-dialog-error mr-2" /></span>}
                    </Modal.Header>
                    <Modal.Body>
                        {body}
                    </Modal.Body>
                    <Modal.Footer>
                        <button type="button" className="btn btn-primary pull-right mr-2" onClick={() => this.props.onClose('Confirm')}>{confirmBtn}</button>
                        {cancelBtn && <button type="button" className="btn btn-secondary pull-right" onClick={() => this.props.onClose('Cancel')}>{cancelBtn}</button>}
                        <div className="clearfix"></div>
                    </Modal.Footer>
                </Modal>
            </React.Fragment>
        );
    }
}

PopUp.propTypes = {
    title: PropTypes.any,
    body: PropTypes.any.isRequired,
    confirmBtn: PropTypes.any.isRequired,
    cancelBtn: PropTypes.any,
    onClose: PropTypes.func.isRequired,
    show: PropTypes.bool,
    display: PropTypes.string
}

export default PopUp;