import * as types from '../../shared/constants';
import initialState from './initialState';

const initReducer = (state = initialState.initForm, action) => {
    switch (action.type) {
        case types.INIT_FILE_PROCESSING_REQUESTS_STARTED:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case types.INIT_FILE_PROCESSING_REQUESTS_SUCCEEDED:
            return {
                ...state,
                initfileProcessingRequestsPayload: action.initFileProcessingRequestsPayload,
                isLoading: false,
                error: false
            }
        case types.INIT_FILE_PROCESSING_REQUESTS_FAILED:
            return {
                ...state,
                isLoading: false,
                initfileProcessingRequestsError: action.initFileProcessingRequestsError,
                error: action.getFileProcessingRequestsError
            }


        case types.RUN_INIT_JOB_STARTED:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case types.RUN_INIT_JOB_SUCCEEDED:
            return {
                ...state,
                runInitJobPayload: action.runInitJobPayload,
                isLoading: false,
                error: false
            }
        case types.RUN_INIT_JOB_FAILED:
            return {
                ...state,
                isLoading: false,
                error: action.runInitJobError
            }
        default:
            return state;
    }
}

export default initReducer;